

















































































































































import { Vue, Component } from "vue-property-decorator";

@Component
export default class FinancialForm extends Vue {
  patientID: string = "";
  firstName: string = "";
  lastName: string = "";
  CreateDate: string = "";
  financialItem: string = "";
  action: string = "add";
  data() {
    return {
      picker: new Date().toISOString().substr(0, 10),
      treatDate: new Date().toISOString().substr(0, 10),
      CreateDate: "",
      treatMenu: false,
      birthMenu: false
    };
  }
  clear() {
    this.patientID = "";
    this.firstName = "";
    this.lastName = "";
    this.CreateDate = "";
    this.financialItem = "";
  }
}
